<template>
    <div>
        <h2>用户列表</h2>
        <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column
                prop="_id"
                label="ID"
                width="270">
            </el-table-column>
            <el-table-column
                prop="username"
                label="用户名"
                width="180">
            </el-table-column>
            <el-table-column
                fixed="right"
                label="操作"
                width="100">
                <template slot-scope="scope">
                    <el-button @click="$router.push(`/admin_user/edit/${scope.row._id}`)" type="text" size="small">编辑</el-button>
                    <el-button @click="move(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: "AdminUserList",
        inject: ['reload'],
        data(){
            return{
                tableData:[]
            }
        },
        created() {
            this.getData();
        },
        methods: {
            async getData(){
                let items = await this.$http.get('/rest/admin_users');
               this.tableData = items.data;
            },
            move(row){  //删除某一项
                this.$confirm(`此操作将永久删除${row.username}, 是否继续?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                    }).then(() => {
                        this.$http.delete(`/rest/admin_users/${row._id}`).then(res=>{
                            this.reload(); // 更新页面
                        })
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                });
            }
        },
    }
</script>

<style lang="stylus" scoped ref="stylesheet/stylus">

</style>